import React from "react";
import styles from "./WelfareListHeader.module.css";

const WelfareListHeader = ({
  totalItems,
  itemsPerPage,
  setItemsPerPage,
  layout,
  setLayout,
}) => {
  return (
    <div className={styles.headerContainer}>
      <div className={styles.totalItems}> : {totalItems}</div>

      <div className={styles.controls}>
        <label>
            :
          <select
            value={itemsPerPage}
            onChange={(e) => setItemsPerPage(Number(e.target.value))}
            className={styles.select}
          >
            <option value={24}>24</option>
            <option value={48}>48</option>
            <option value={96}>96</option>
          </select>
        </label>

        <button
          className={`${styles.iconButton} ${
            layout === "grid" ? styles.active : ""
          }`}
          onClick={() => setLayout("grid")}
        >
          <img
            src="/assets/gridIcon.png"
            alt="Grid View"
            className={styles.icon}
          />
        </button>

        <button
          className={`${styles.iconButton} ${
            layout === "list" ? styles.active : ""
          }`}
          onClick={() => setLayout("list")}
        >
          <img
            src="/assets/listIcon.png"
            alt="List View"
            className={styles.icon}
          />
        </button>
      </div>
    </div>
  );
};

export default WelfareListHeader;
